html {
  box-sizing: border-box;
}

body {
  color: #fff;
  background: linear-gradient(164deg, #6d5c69 0%, #2c2b2a 100%) fixed;
  font-family: sans-serif;
}

#sawayama {
  z-index: -10;
  font-family: serif;
  font-size: 64px;
  font-weight: bold;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#sawayama .shine {
  width: 40px;
  height: 2px;
  z-index: 10;
  background: radial-gradient(50% 5%, #fdfbf9ee 0%, #fdfbf988 12%, #fdfbf911 99%, #fdfbf900);
  position: absolute;
  top: 17px;
  left: -13px;
}

#sawayama .shine.vertical {
  width: 50px;
  left: -18px;
  transform: rotate(90deg);
}

#sawayama .shine.diag1 {
  width: 30px;
  left: -8px;
  transform: rotate(45deg);
}

#sawayama .shine.diag2 {
  width: 30px;
  left: -8px;
  transform: rotate(-45deg);
}

#sawayama .letter {
  background: linear-gradient(170deg, #f7c588 15%, #a59478 25%, #8b744e 30%, #d8a761 71%) text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #0000;
  filter: drop-shadow(0 -1px #fdfbf9) drop-shadow(2px 2px #222120);
  width: 64px;
}

#powerlance-bg {
  z-index: -10;
  width: 1280px;
  height: 720px;
  position: fixed;
  top: 0;
  left: 0;
}

#powerlance-bg .red-stripe-1 {
  height: 20px;
  transform-origin: 0 100%;
  width: 100%;
  z-index: 0;
  background: #da4d43;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(-45deg);
  box-shadow: 0 1px #2c2b2a, inset 1px 1px #fff8;
}

#powerlance-bg .red-stripe-2 {
  height: 200px;
  transform-origin: 0 100%;
  width: 100%;
  z-index: 10;
  background: #da4d43;
  position: absolute;
  bottom: 177px;
  left: 150px;
  transform: rotate(-45deg)skew(-45deg);
  box-shadow: inset 1px 1px #fff8;
}

#powerlance-bg .text {
  transform-origin: 0 100%;
  width: 100%;
  z-index: 20;
  width: 425px;
  position: absolute;
  bottom: 387px;
  left: 370px;
  transform: rotate(-45deg);
}

#powerlance-bg .grey-stripe {
  opacity: .3;
