#app {
  max-width: 960px;
  margin: 20px auto;
}

.window {
  clip-path: polygon(0 11px, 11px 0, 100% 0, 100% 100%, 100% 100%, 0 100%);
  border: 1px solid #fff;
  border-top: none;
  border-radius: 0 2px 2px;
  flex-direction: column;
  display: flex;
  position: relative;
}

.window-header-wrapper {
  clip-path: polygon(0 10px, 10px 0, 100% 0, 100% 100%, 100% 100%, 0 100%);
  background: #fff;
  border-radius: 0 2px 0 0;
  flex: none;
  padding: 1px 0 0;
}

.window-header {
  height: 44px;
  clip-path: polygon(0 10px, 10px 0, 100% 0, 100% 100%, 0 100%);
  background: #a4aaaf;
  border-bottom: 1px solid #282932;
  border-radius: 0 2px 0 0;
  flex-direction: row;
  display: flex;
}

.window-title {
  text-transform: uppercase;
  color: #ffea1f;
  background: #0101b2;
  border-radius: 4px;
  flex: none;
  margin: 8px 14px;
  padding: 4px 8px;
  font-family: monospace;
  font-size: 16px;
  box-shadow: -1px -1px #586468, 1px 1px #cddee1;
}

.window-header-line {
  height: 16px;
  border-bottom: 1px solid #282932;
  flex: 1;
  margin: 0 40px 0 10px;
  position: relative;
  box-shadow: 0 1px #cddee1;
}

.window-header-line:before {
  content: "";
  width: 30px;
  height: 30px;
  clip-path: polygon(0 0, 110% 110%, 0 110%);
  border-bottom: 1px solid #282932;
  display: block;
  position: absolute;
  top: -20px;
  left: -15px;
  transform: rotate(45deg);
  box-shadow: 0 1px #cddee1;
}

.window-header-line:after {
  content: "";
  width: 40px;
  height: 40px;
  clip-path: polygon(0 0, 110% 110%, 0 110%);
  border-bottom: 1px solid #282932;
  display: block;
  position: absolute;
  bottom: -21px;
  right: -48px;
  transform: rotate(45deg);
  box-shadow: 0 1px #cddee1;
}

.window-contents {
  background: #a4aaaf;
  border: 1px solid #525959;
  border-radius: 0 0 2px 2px;
  flex: 1;
  padding: 10px;
}

.main-window .window-contents {
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  background-image: linear-gradient(45deg, #575d62 28.57%, #3b454d 28.57% 35.71%, #575d62 35.71% 42.86%, #657581 42.86% 50%, #575d62 50% 78.57%, #3b454d 78.57% 85.71%, #575d62 85.71% 92.86%, #657581 92.86% 100%);
  background-size: 19.8px 19.8px;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 200px 1fr;
  display: grid;
}

.tools-window {
  grid-area: 1 / 1 / 2 / 2;
}

.palette-window {
  grid-area: 2 / 1 / 3 / 2;
}

.map-window {
  grid-area: 1 / 2 / 3 / 3;
}

.tools-window .window-contents, .palette-window .window-contents, .map-window .window-contents {
  background: #a4aaaf;
}

.window.settings .window-contents {
  color: #2c3234;
  background: #726f6d;
  border: 1px solid #a2c4d2;
  border-top: none;
  box-shadow: -1px 0 #525959;
}

.window.settings .window-contents h2 {
  color: #1c272a;
  text-transform: uppercase;
  background: #525959;
  border-bottom: 1px solid #616b6f;
  margin: -10px -10px 10px;
  padding: 20px;
  font-family: monospace;
  font-size: 28px;
  box-shadow: 0 1px 1px #414a46;
}

#characters {
  display: none;
}

.map-container {
  overflow: auto;
}

#map-canvas, #tileset-canvas {
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-crisp-edges;
  image-rendering: pixelated;
  background-color: #192f30;
}

